<template>
  <div class="sl-col" 
    :class="[ span ? 'sl-col-' + span : '', 
      xs ? `sl-col-xs-${xs}` : ``,
      sm ? `sl-col-sm-${sm}` : ``,
      md ? `sl-col-md-${md}` : ``,
      lg ? `sl-col-lg-${lg}` : ``,
      xl ? `sl-col-xl-${xl}` : ``,
      offset ? `sl-col-offset-${offset}` : ``
     ]" 
    :style="{ 'padding-right': gutter ? gutter / 2 + 'px' : '', 'padding-left': gutter ? gutter / 2 + 'px' : '' }">
    <slot></slot>
  </div>
</template>
<script>
let validator = (value) => {
    let keys = Object.keys(value)
    let valid = true
    keys.forEach(key => {
        if (!['span', 'offset'].includes(key)) {
            valid = false
        }
    })
    console.log(valid)
    return valid
}
export default {
  name: 'slCol',
  data(){
    return {
      gutter: 0,
    }
  },
  created(){
    // console.log(this.gutter)
  },
  components: {},
  props: {
    span: {
      type: Number | String,
      default: 0
    },
    xs: {
      type: Number | String,
      default: 0
    },
    sm: {
      type: Number | String,
      default: 0
    },
    md: {
      type: Number | String,
      default: 0
    },
    lg: {
      type: Number | String,
      default: 0
    },
    xl: {
      type: Number | String,
      default: 0
    },
    offset: {
      type: Number | String,
      default: 0
    }
  }
}
</script>
<style lang="scss" scoped>
  [class*=sl-col-] {
    box-sizing: border-box;
    float: left;
  }
  .sl-col-24 {
    width: 100%;
  }
  .sl-col-12 {
    width: 50%;
  }
  .sl-col-8 {
    width: 33.33333%;
  }
  .sl-col-6 {
    width: 25%;
  }
  .sl-col-4 {
    width: 16.66667%;
  }

</style>


